export const Brush = (props) => {
  const ref = useRef(null)

  useLayoutEffect(() => {
    const svg = ref.current
    const bbox = svg.getBBox()
    const viewBox = [bbox.x, bbox.y, bbox.width, bbox.height].join(' ')
    svg.setAttribute('viewBox', viewBox)
  }, [])

  function renderBrush(type, color) {
    switch (type) {
      case 1:
        return brush1(color)
      case 2:
        return brush2(color)
      case 3:
        return brush3(color)
      case 4:
        return brush4(color)
      case 5:
        return brush5(color)
      case 6:
        return brush6(color)
      case 7:
        return brush7(color)
      case 8:
        return brush8(color)
      default:
        return brush1(color)
    }
  }

  return (
    <span
      style={{
        position: 'relative',
        padding: '7px 58px',
        ...(props.style || {})
      }}
    >
      <svg
        ref={ref}
        id='svg-brush'
        xmlns='http://www.w3.org/2000/svg'
        version='1.1'
        x='0px'
        y='0px'
        viewBox='0 0 1100 750'
        style={{ position: 'absolute', inset: 0, zIndex: -1 }}
        width='auto'
        height='auto'
        enableBackground='new 0 0 1100 500'
        preserveAspectRatio='none'
      >
        {renderBrush(props.type, props.color)}
      </svg>
      {props.children}
    </span>
  )
}
